import { Component, ContentChild, Directive, Input, TemplateRef } from '@angular/core';
import { DefaultPaymentCardNumberFormat, PaymentCardNumberFormat } from '@backbase/ui-ang/payment-card-number-pipe';

@Directive({ selector: 'ng-template[bbCustomProductItemCreditCardTitle]' })
export class CustomProductItemCreditCardTitleDirective {
  constructor(public templateRef: TemplateRef<any>) {}
}

@Directive({ selector: 'ng-template[bbCustomProductItemCreditCardDetailsFormatter]' })
export class CustomProductItemCreditCardDetailsFormatterDirective {
  constructor(public templateRef: TemplateRef<any>) {}
}

@Directive({ selector: 'ng-template[bbCustomProductItemCreditCardMainSection]' })
export class CustomProductItemCreditCardMainSectionDirective {
  constructor(public templateRef: TemplateRef<any>) {}
}

@Directive({ selector: 'ng-template[bbCustomProductItemCreditCardSecondarySection]' })
export class CustomProductItemCreditCardSecondarySectionDirective {
  constructor(public templateRef: TemplateRef<any>) {}
}

@Component({
  selector: 'bb-product-item-credit-card-ui',
  templateUrl: './product-item-credit-card.component.html',
})
export class ProductItemCreditCardComponent {
  @ContentChild(CustomProductItemCreditCardTitleDirective, { static: true })
  customProductItemCreditCardTitle: CustomProductItemCreditCardTitleDirective | undefined;

  @ContentChild(CustomProductItemCreditCardDetailsFormatterDirective, { static: true })
  customProductItemCreditCardDetailsFormatter: CustomProductItemCreditCardDetailsFormatterDirective | undefined;

  @ContentChild(CustomProductItemCreditCardMainSectionDirective, { static: true })
  customProductItemCreditCardMainSection: CustomProductItemCreditCardMainSectionDirective | undefined;

  @ContentChild(CustomProductItemCreditCardSecondarySectionDirective, { static: true })
  customProductItemCreditCardSecondarySection: CustomProductItemCreditCardSecondarySectionDirective | undefined;

  readonly hostRef: ProductItemCreditCardComponent = this;
  /**
   * The card Title value. Default is empty string
   */
  @Input() title: string | undefined;
  /**
   * The product Number. Default is empty string
   */
  @Input() productNumber: string | undefined;
  /**
   * Configuration of how the product number should be formatted.
   */
  @Input() productNumberFormat: PaymentCardNumberFormat = DefaultPaymentCardNumberFormat;
  /**
   * The card currency value. Default is empty string
   */
  @Input() currency?: string | undefined;
  /**
   * The card Amount value
   */
  @Input() amount?: number | undefined;
  /**
   * The card additional main Section currency value
   */
  @Input() mainSectionCurrency?: string | undefined;
  /**
   * The card additional main Section Amount value
   */
  @Input() mainSectionAmount?: number | undefined;
  /**
   * The card additional main Section Amount value
   */
  @Input() mainSectionTitle?: string | undefined;
  /**
   * The card additional Secondary Section currency value
   */
  @Input() secondarySectionCurrency?: string | undefined;
  /**
   * The card additional Secondary Section Amount value
   */
  @Input() secondarySectionAmount?: number | undefined;
  /**
   * The card additional Secondary Section Amount value
   */
  @Input() secondarySectionTitle?: string | undefined;
  /**
   * The card highlight the amount and currency value
   */
  @Input() highlight?: boolean;
  /**
   * The product details info values
   */
  @Input()
  productDetailsInfo?: {
    [key: string]: any;
  };
  /**
   * The active state of card (it adds active class for blue line)
   */
  @Input() active = true;
  /**
   * The favorite state of card (add favorite icon next to title)
   */
  @Input() favorite = false;
  /**
   * The favorite icon color (define star icon color)
   */
  @Input() favoriteIconColor = 'primary';
}
